import Vue from 'vue'
import App from './App.vue'
import VueRouter from 'vue-router'
import routes from './routerConfig.js'
import stores from './store/store.js'

Vue.use(VueRouter);

const router = new VueRouter({
	routes,
	mode: 'history' //history
	//scrollBehavior: () => ({ y: 0 }) // 
});

new Vue({
  el: '#app',
  render: h => h(App),
  router,
  store: stores
})



